import { action, makeObservable, observable } from 'mobx';

class MoodStore {
    @observable moods = ['default', 'angry'];

    constructor() {
        makeObservable(this);
    }

    @action addMood = (mood: string) => {
        this.moods = [...this.moods, mood];
    };

    @action removeMood = (mood: string) => {
        this.moods = this.moods.filter((m) => m !== mood);
    };
}

export const moodStore = new MoodStore();
