import { action, makeObservable, observable } from 'mobx';

class CoordsStore {
    @observable coords: [number, number] = [0, 0];

    constructor() {
        makeObservable(this);
    }

    @action setCoords(coords: [number, number]) {
        this.coords = coords;
    }
}

export const coordsStore = new CoordsStore();
