import { action, makeObservable, observable } from 'mobx';

class LinkStore {
    @observable start?: number;
    @observable option?: string | number;

    constructor() {
        makeObservable(this);
    }

    @action setStart(start: number | undefined, option?: string | number) {
        this.start = start;
        this.option = option;
    }

    @action resetStart() {
        this.setStart(undefined);
    }
}

export const linkStore = new LinkStore();
