import s from './styles.module.scss';
import { coordsStore } from '../../stores/coords-store';
import { observer } from 'mobx-react';
import { Close } from '@mui/icons-material';
import { blockStore } from '../../stores/block-store';
import { FC } from 'react';

interface LinkProps {
    id: number;
    option: string;
    x1?: number;
    y1?: number;
    x2?: number;
    y2?: number;
}

export const Link: FC<LinkProps> = observer(
    ({ id, option, x1 = 0, y1 = 0, x2 = 0, y2 = 0 }) => {
        return (
            <div
                className={s.link}
                style={{
                    left:
                        coordsStore.coords[0] +
                        x1 -
                        (x2 - x1 < 0 ? x1 - x2 : 0),
                    top:
                        coordsStore.coords[1] +
                        y1 -
                        (y2 - y1 < 0 ? y1 - y2 : 0),
                    width: Math.abs(x2 - x1) + 1,
                    height: Math.abs(y2 - y1) + 1,
                    transform: `scale(${Math.sign(x2 - x1) || 1}, ${
                        Math.sign(y2 - y1) || 1
                    })`,
                }}
            >
                <div
                    className={s.close}
                    onClick={() => blockStore.removeLink(id, option)}
                >
                    <Close />
                </div>
            </div>
        );
    }
);
