import { action, autorun, makeObservable, observable } from 'mobx';
import { linkStore } from './link-store';
import { coordsStore } from './coords-store';
import { localStorageStore } from './local-storage-store';
import { dialogStore } from './dialog-store';

export enum BlockType {
    start = 'start',
    line = 'line',
    trigger = 'trigger',
}

export interface Block<T = any> {
    id: number;
    x?: number;
    y?: number;
    startX?: number;
    startY?: number;
    endX?: number;
    endY?: number;
    content?: T;
    type: BlockType;
    links?: Record<string, number>;
    optionsCoords?: Record<string, [number, number]>;
}

export const languages = ['EN', 'RU'];
type Language = string;

interface LineContent {
    character: number | '';
    texts: Record<Language, string>;
    mood: string;
    options?: Record<string, string>[];
    currentOptionId: number;
}

interface TriggerContent {
    text: string;
}

export type LineBlock = Block<LineContent>;
export type TriggerBlock = Block<TriggerContent>;
export type StartBlock = Block<undefined>;

const defaultContent = {
    [BlockType.line]: {
        character: '',
        texts: languages.reduce((a, l) => ({ ...a, [l]: '' }), {}),
        mood: 'default',
        options: [],
        currentOptionId: 0,
    },
    [BlockType.trigger]: {
        text: '',
    },
    [BlockType.start]: undefined,
};

const defaultBlocks = [
    {
        id: 0,
        type: BlockType.start,
        x: 50,
        y: 50,
    },
];
const defaultCurrentId = 1;

class BlockStore {
    @observable blocks: Block[] = defaultBlocks;
    @observable currentId = defaultCurrentId;

    constructor() {
        makeObservable(this);

        autorun(() => {
            if (!dialogStore.saveInProgress) {
                localStorageStore.saveDialog(dialogStore.id);
            }
        });
    }

    @action setBlocks(blocks: Block[]) {
        this.blocks = blocks;
    }

    @action setCurrentId(id: number) {
        this.currentId = id;
    }

    getBlock(id: number) {
        return this.blocks.find((block) => block.id === id);
    }

    @action removeBlock(id: number, withLinks = false) {
        this.blocks = this.blocks.filter((block) => block.id !== id);

        if (!withLinks) {
            return;
        }

        this.blocks.forEach((b) => {
            Object.entries(b.links || {}).forEach((entry) => {
                if (entry[1] === id) {
                    this.removeLink(b.id, entry[0]);
                }
            });
        });
    }

    @action changeLineText(id: number, lang: string, text: string) {
        const block = this.getBlock(id);
        if (block) {
            this.removeBlock(id);
            this.blocks = [
                ...this.blocks,
                {
                    ...block,
                    content: {
                        ...block.content,
                        texts: { ...block.content.texts, [lang]: text },
                    },
                },
            ];
        }
    }

    @action changeLineMood(id: number, mood: string) {
        const block = this.getBlock(id);
        if (block) {
            this.removeBlock(id);
            this.blocks = [
                ...this.blocks,
                { ...block, content: { ...block.content, mood } },
            ];
        }
    }

    @action changeLineCharacter(id: number, character: number) {
        const block = this.getBlock(id);
        if (block) {
            this.removeBlock(id);
            this.blocks = [
                ...this.blocks,
                { ...block, content: { ...block.content, character } },
            ];
        }
    }

    @action changeTriggerText(id: number, text: string) {
        const block = this.getBlock(id);
        if (block) {
            this.removeBlock(id);
            this.blocks = [...this.blocks, { ...block, content: { text } }];
        }
    }

    @action addLink(start: number, option: string | number, id: number) {
        const block = this.getBlock(start);
        if (block) {
            this.removeBlock(start);
            this.blocks = [
                ...this.blocks,
                {
                    ...block,
                    links: { ...(block.links || {}), [option]: id },
                },
            ];
        }
        linkStore.resetStart();
    }

    @action removeLink(id: number, option: string) {
        const block = this.getBlock(id);
        if (block) {
            const newLinks = { ...(block.links || {}) };
            delete newLinks[option];
            this.removeBlock(id);
            this.blocks = [...this.blocks, { ...block, links: newLinks }];
        }
    }

    @action setCoords(
        id: number,
        startX: number,
        startY: number,
        endX: number,
        endY: number,
        optionsCoords?: Record<string, [number, number]>
    ) {
        const block = this.getBlock(id);
        if (block) {
            this.removeBlock(id);
            this.blocks = [
                ...this.blocks,
                { ...block, startX, startY, endX, endY, optionsCoords },
            ];
        }
    }

    @action setBlockCoords(id: number, x: number, y: number) {
        const block = this.getBlock(id);
        if (block) {
            this.removeBlock(id);
            this.blocks = [...this.blocks, { ...block, x, y }];
        }
    }

    @action addBlock(type: BlockType) {
        this.blocks = [
            ...this.blocks,
            {
                x: -coordsStore.coords[0] + 50,
                y: -coordsStore.coords[1] + 50,
                id: this.currentId,
                type,
                content: defaultContent[type],
            },
        ];
        this.setCurrentId(this.currentId + 1);
    }

    @action clear() {
        this.blocks = defaultBlocks;
        this.currentId = defaultCurrentId;
    }

    @action addOption(id: number, option: Record<string, string>) {
        const block = this.getBlock(id);
        if (block) {
            this.removeBlock(id);
            this.blocks = [
                ...this.blocks,
                {
                    ...block,
                    content: {
                        ...block.content,
                        options: [
                            { ...option, _id: block.content.currentOptionId },
                            ...block.content.options,
                        ],
                        currentOptionId: block.content.currentOptionId + 1,
                    },
                },
            ];
        }
    }

    @action removeOption(id: number, hash: string) {
        const block = this.getBlock(id);
        if (block) {
            this.removeBlock(id);
            this.blocks = [
                ...this.blocks,
                {
                    ...block,
                    content: {
                        ...block.content,
                        options: block.content.options.filter(
                            (o: any) => o._id !== hash
                        ),
                    },
                },
            ];
            this.removeLink(id, hash);
        }
    }
}

export const blockStore = new BlockStore();
