import { Draggable } from '../draggable';
import s from './styles.module.scss';
import DragIndicatorIcon from '@mui/icons-material/DragIndicator';
import { FC, ReactNode, useRef } from 'react';
import { linkStore } from '../../stores/link-store';
import { blockStore } from '../../stores/block-store';
import classNames from 'classnames';
import { observer } from 'mobx-react';
import { coordsStore } from '../../stores/coords-store';
import { Block as BlockType } from '../../stores/block-store';

interface BlockProps {
    children: (redrawLinks: () => void) => ReactNode;
    background?: string;
    startCircle?: boolean;
    data: BlockType;
}

export const Block: FC<BlockProps> = observer(
    ({
        children,
        background = 'white',
        startCircle = true,
        data: { id, x, y, content },
    }) => {
        const onClickInputPoint = () => {
            if (linkStore.start !== undefined) {
                blockStore.addLink(
                    linkStore.start,
                    linkStore.option !== undefined
                        ? linkStore.option
                        : '_default',
                    id
                );
            }
        };

        const onClockOutputPoint = () => {
            linkStore.setStart(id, '_default');
        };

        const lastCircleStyles = classNames(s.circle, {
            [s.activeCircle]:
                linkStore.start === id && linkStore.option === '_default',
        });

        const startRef = useRef<HTMLDivElement>(null);
        const endRef = useRef<HTMLDivElement>(null);

        const onDrag = () => {
            const startBb = startRef.current?.getBoundingClientRect();
            const endBB = endRef.current?.getBoundingClientRect();
            let optionsBB;
            if (content?.options) {
                optionsBB = content.options.map(
                    (option: Record<string, string>) => [
                        option._id,
                        document
                            .querySelector('.link-' + id + '-' + option._id)
                            ?.getBoundingClientRect(),
                    ]
                );
            }
            if (startBb && endBB) {
                blockStore.setCoords(
                    id,
                    -coordsStore.coords[0] + startBb.x + startBb.width / 2,
                    -coordsStore.coords[1] + startBb.y + startBb.height / 2,
                    -coordsStore.coords[0] + endBB.x + endBB.width / 2,
                    -coordsStore.coords[1] + endBB.y + endBB.height / 2,
                    optionsBB
                        ?.map((entry: any) => [
                            entry[0],
                            [
                                -coordsStore.coords[0] +
                                    entry[1].x +
                                    entry[1].width / 2,
                                -coordsStore.coords[1] +
                                    entry[1].y +
                                    entry[1].height / 2,
                            ],
                        ])
                        .reduce(
                            (a: object, entry: any) => ({
                                ...a,
                                [entry[0]]: entry[1],
                            }),
                            {}
                        )
                );
            }
        };

        return (
            <Draggable
                onDrag={onDrag}
                x={x}
                y={y}
                id={id}
                setCoords={(x, y) => blockStore.setBlockCoords(id, x, y)}
            >
                {(ref, drag) => (
                    <div
                        className={s.block}
                        style={{ backgroundColor: background }}
                    >
                        <div className={s.container}>
                            <div
                                className={s.circleContainer}
                                style={{
                                    visibility: startCircle
                                        ? 'visible'
                                        : 'hidden',
                                }}
                            >
                                <div
                                    className={s.circle}
                                    onClick={onClickInputPoint}
                                    ref={startRef}
                                />
                            </div>
                            <div
                                ref={ref}
                                className={s.draggable}
                                style={{ cursor: drag ? 'grabbing' : 'grab' }}
                            >
                                <DragIndicatorIcon />
                            </div>
                            <div className={s.content}>{children(onDrag)}</div>
                            <div className={s.circleContainer}>
                                <div
                                    className={lastCircleStyles}
                                    onClick={onClockOutputPoint}
                                    ref={endRef}
                                />
                            </div>
                        </div>
                    </div>
                )}
            </Draggable>
        );
    }
);
