import s from './styles.module.scss';
import {
    Accordion,
    AccordionDetails,
    AccordionSummary,
    Chip,
    Divider,
    FormControl,
    InputLabel,
    MenuItem,
    Select,
    TextField,
    Tooltip,
    Typography,
} from '@mui/material';
import { Block } from '../block/block';
import { Add, Delete, ExpandMore, QuestionMark } from '@mui/icons-material';
import { blockStore, languages, LineBlock } from '../../stores/block-store';
import React, { FC, Fragment, useState } from 'react';
import { characterStore } from '../../stores/characters-store';
import { linkStore } from '../../stores/link-store';
import classNames from 'classnames';
import { observer } from 'mobx-react';
import { dialogStore } from '../../stores/dialog-store';

interface LineProps {
    data: LineBlock;
}

export const Line: FC<LineProps> = observer(
    ({ data: { id, content, ...rest } }) => {
        const [newOption, setNewOption] = useState<Record<string, string>>({});

        return (
            <Block data={{ id, content, ...rest }}>
                {(redrawLinks) => (
                    <>
                        <div className={classNames(s.content, s.oddPadding)}>
                            <Typography variant="h5"></Typography>
                            <FormControl fullWidth>
                                <InputLabel id="line-label">
                                    
                                </InputLabel>
                                <Select
                                    labelId="line-label"
                                    label=""
                                    value={content?.character}
                                    onChange={(e) =>
                                        blockStore.changeLineCharacter(
                                            id,
                                            e.target.value as number
                                        )
                                    }
                                >
                                    {characterStore.characters.map((ch) => (
                                        <MenuItem key={ch.id} value={ch.id}>
                                            {Object.entries(ch.names)[0][1]}
                                        </MenuItem>
                                    ))}
                                </Select>
                            </FormControl>
                            {Object.entries(content?.texts || {}).map(
                                (entry) => (
                                    <TextField
                                        key={entry[0]}
                                        label={` [${entry[0]}]`}
                                        placeholder=" ..."
                                        value={entry[1]}
                                        multiline
                                        onChange={(e) => {
                                            blockStore.changeLineText(
                                                id,
                                                entry[0],
                                                e.target.value
                                            );
                                            redrawLinks();
                                        }}
                                    />
                                )
                            )}
                            <Tooltip
                                title="** -  -  "
                                style={{ width: 100 }}
                            >
                                <Chip
                                    size="small"
                                    avatar={<QuestionMark />}
                                    label=""
                                />
                            </Tooltip>
                            <FormControl fullWidth>
                                <InputLabel id="mood-label">
                                    
                                </InputLabel>
                                <Select
                                    labelId="mood-label"
                                    label=""
                                    value={content?.mood}
                                    onChange={(e) =>
                                        blockStore.changeLineMood(
                                            id,
                                            e.target.value
                                        )
                                    }
                                >
                                    {content?.character !== undefined &&
                                    content?.character !== '' ? (
                                        characterStore
                                            .getCharacter(content.character)
                                            ?.moods.map((mood) => (
                                                <MenuItem
                                                    key={mood}
                                                    value={mood}
                                                >
                                                    {mood}
                                                </MenuItem>
                                            ))
                                    ) : (
                                        <MenuItem value="default">
                                            default
                                        </MenuItem>
                                    )}
                                </Select>
                            </FormControl>
                        </div>
                        <Divider />
                        <Accordion disableGutters onTransitionEnd={redrawLinks}>
                            <AccordionSummary expandIcon={<ExpandMore />}>
                                  
                            </AccordionSummary>
                            <AccordionDetails>
                                <div
                                    className={classNames(
                                        s.content,
                                        s.noPadding
                                    )}
                                >
                                    {languages.map((language) => (
                                        <div key={language}>
                                            <TextField
                                                value={
                                                    newOption[language] || ''
                                                }
                                                fullWidth
                                                label={
                                                    '   [' +
                                                    language +
                                                    ']'
                                                }
                                                onChange={(e) =>
                                                    setNewOption({
                                                        ...newOption,
                                                        [language]:
                                                            e.target.value,
                                                    })
                                                }
                                            />
                                        </div>
                                    ))}
                                    <Chip
                                        size="small"
                                        avatar={<Add />}
                                        label="   "
                                        onClick={() => {
                                            blockStore.addOption(id, {
                                                ...newOption,
                                                _id: String(Date.now()),
                                            });
                                            setNewOption({});
                                            redrawLinks();
                                        }}
                                    />
                                </div>
                            </AccordionDetails>
                        </Accordion>
                        <Divider />
                        {content?.options &&
                            content.options.map((option) => (
                                <Fragment key={option.id}>
                                    <div className={s.mood}>
                                        <div className={s.leftGroup}>
                                            <Delete
                                                onClick={() =>
                                                    blockStore.removeOption(
                                                        id,
                                                        option._id
                                                    )
                                                }
                                            />
                                            <div>
                                                {Object.entries(option)
                                                    .filter(
                                                        (e) => e[0] !== '_id'
                                                    )
                                                    .map((entry) => (
                                                        <Fragment
                                                            key={entry[0]}
                                                        >
                                                            <span
                                                                style={{
                                                                    color: 'gray',
                                                                    fontSize: 14,
                                                                }}
                                                            >
                                                                {entry[0]}:
                                                            </span>{' '}
                                                            {entry[1]}{' '}
                                                        </Fragment>
                                                    ))}
                                            </div>
                                        </div>
                                        <div
                                            className={classNames(
                                                s.circle,
                                                {
                                                    [s.activeCircle]:
                                                        linkStore.start ===
                                                            id &&
                                                        linkStore.option ===
                                                            option._id,
                                                },
                                                'link-' + id + '-' + option._id
                                            )}
                                            onClick={() =>
                                                linkStore.setStart(
                                                    id,
                                                    option._id
                                                )
                                            }
                                        />
                                    </div>
                                    <Divider />
                                </Fragment>
                            ))}
                        <div className={s.content}>
                            <Chip
                                size="small"
                                avatar={<Delete />}
                                label=""
                                onClick={() =>
                                    dialogStore.openDialog({
                                        title: '',
                                        text: '    ?',
                                        onAgree: () =>
                                            blockStore.removeBlock(id, true),
                                    })
                                }
                            />
                        </div>
                    </>
                )}
            </Block>
        );
    }
);
