import s from './styles.module.scss';
import { Chip, TextField, Typography } from '@mui/material';
import { Block } from '../block/block';
import React, { FC } from 'react';
import { Delete } from '@mui/icons-material';
import { blockStore, TriggerBlock } from '../../stores/block-store';
import { dialogStore } from '../../stores/dialog-store';

interface TriggerProps {
    data: TriggerBlock;
}

export const Trigger: FC<TriggerProps> = ({
    data: { id, content, ...rest },
}) => {
    return (
        <Block background="lightgreen" data={{ id, content, ...rest }}>
            {(redrawLinks) => (
                <div className={s.trigger}>
                    <Typography variant="h5"></Typography>
                    <TextField
                        label=""
                        multiline
                        value={content?.text}
                        onChange={(e) => {
                            blockStore.changeTriggerText(id, e.target.value);
                            redrawLinks();
                        }}
                    />
                    <Chip
                        size="small"
                        avatar={<Delete />}
                        label=""
                        onClick={() =>
                            dialogStore.openDialog({
                                title: '',
                                text: '    ?',
                                onAgree: () => blockStore.removeBlock(id, true),
                            })
                        }
                    />
                </div>
            )}
        </Block>
    );
};
