import { blockStore } from './block-store';
import { characterStore } from './characters-store';
import { dialogStore } from './dialog-store';

class LocalStorageStore {
    saveDialog(id: number | string = 'current') {
        localStorage.setItem(
            'dialog:' + id,
            JSON.stringify({
                name: dialogStore.name,
                blocks: blockStore.blocks,
                blocksId: blockStore.currentId,
            })
        );
    }

    reloadDialog(id: number | undefined) {
        const data = localStorage.getItem(
            'dialog:' + (id === undefined ? 'current' : id)
        );
        if (data) {
            dialogStore.setId(id);
            const { name, blocks, blocksId } = JSON.parse(data);
            dialogStore.setName(name);
            blockStore.setBlocks(blocks);
            blockStore.setCurrentId(blocksId);
        }
    }

    saveCharacters() {
        localStorage.setItem(
            'characters',
            JSON.stringify(characterStore.characters)
        );
    }

    reloadCharacters() {
        const data = localStorage.getItem('characters');
        if (data) {
            characterStore.setCharacters(JSON.parse(data));
        }
    }

    getSaves = (): { id: number; name: string }[] => {
        return JSON.parse(localStorage.getItem('saves') || '[]');
    };

    saveCurrentDialog(name: string) {
        const saves = this.getSaves();
        const saveId = Number(localStorage.getItem('saveId'));
        localStorageStore.saveDialog(saveId);
        localStorage.setItem('saveId', String(saveId + 1));
        localStorage.setItem(
            'saves',
            JSON.stringify([
                ...saves,
                {
                    id: saveId,
                    name,
                },
            ])
        );
        dialogStore.setId(saveId);
        this.setCurrentDialog(saveId);
        localStorage.removeItem('dialog:current');
    }

    //      ,    
    removeSave(id: number, noReset = false) {
        const saves = this.getSaves();
        localStorage.setItem(
            'saves',
            JSON.stringify(saves.filter((save: any) => save.id !== id))
        );
        if (!noReset && id === dialogStore.id) {
            dialogStore.setId(undefined);
            this.setCurrentDialog(undefined);
        }
    }

    editSave(id: number, name: string) {
        this.removeSave(id, true);
        localStorage.setItem(
            'saves',
            JSON.stringify([{ id, name }, ...this.getSaves()])
        );
    }

    getCurrentDialogId(): number | undefined {
        const id = localStorage.getItem('currentDialogId');
        return id !== null ? Number(id) : undefined;
    }

    setCurrentDialog(id: number | undefined) {
        if (id !== undefined) {
            localStorage.setItem('currentDialogId', String(id));
        } else {
            localStorage.removeItem('currentDialogId');
        }
    }
}

export const localStorageStore = new LocalStorageStore();
