import { action, makeObservable, observable, reaction } from 'mobx';
import { localStorageStore } from './local-storage-store';

interface MUIDialogContent {
    title: string;
    text: string;
    onAgree: () => void;
    onDisagree?: () => void;
}

interface MUIDialog extends MUIDialogContent {
    open: boolean;
}

class DialogStore {
    @observable id: number | undefined = undefined;
    @observable name = '';
    @observable saveInProgress = false;

    // 
    @observable dialog: MUIDialog | null = null;

    constructor() {
        makeObservable(this);
    }

    @action setSaveInProgress(sip: boolean) {
        this.saveInProgress = sip;
    }

    @action setName(name: string) {
        this.name = name;
    }

    @action setId(id: number | undefined) {
        this.id = id;
    }

    @action clear() {
        this.name = '';
        this.id = undefined;
    }

    @action openDialog(dialog: MUIDialogContent) {
        this.dialog = { ...dialog, open: true };
    }

    @action closeDialog() {
        if (!this.dialog) return;
        this.dialog = { ...this.dialog, open: false };
    }

    @action dialogAgree = () => {
        if (!this.dialog) return;
        this.dialog.onAgree();
        this.closeDialog();
    };

    @action dialogDisagree = () => {
        if (!this.dialog) return;
        this.dialog.onDisagree?.();
        this.closeDialog();
    };
}

export const dialogStore = new DialogStore();
