import { action, makeObservable, observable, reaction } from 'mobx';
import { localStorageStore } from './local-storage-store';

export interface Character {
    id: number;
    names: Record<string, string>;
    moods: string[];
}

class CharactersStore {
    @observable characters: Character[] = [];
    currentId: number = 0;

    constructor() {
        makeObservable(this);

        reaction(
            () => this.characters,
            () => localStorageStore.saveCharacters()
        );
    }

    @action setCharacters(characters: Character[]) {
        this.characters = characters;
    }

    @action addCharacter(names: Record<string, string>, moods = ['default']) {
        this.characters = [
            ...this.characters,
            { id: this.currentId, names, moods },
        ];
        this.currentId++;
    }

    @action removeCharacter(id: number) {
        this.characters = this.characters.filter((ch) => ch.id !== id);
    }

    getCharacter(id: number) {
        return this.characters.find((ch) => ch.id === id);
    }

    @action changeName(id: number, lang: string, name: string) {
        const char = this.characters.findIndex((ch) => ch.id === id);
        if (char !== -1) {
            const newCharacters = [...this.characters];
            newCharacters[char] = {
                ...this.characters[char],
                names: { ...this.characters[char].names, [lang]: name },
            };
            this.characters = newCharacters;
        }
    }

    @action addMood(id: number, mood: string) {
        const char = this.characters.findIndex((ch) => ch.id === id);
        if (char !== -1) {
            const newCharacters = [...this.characters];
            newCharacters[char] = {
                ...this.characters[char],
                moods: [...this.characters[char].moods, mood],
            };
            this.characters = newCharacters;
        }
    }

    @action removeMood(id: number, mood: string) {
        const char = this.characters.findIndex((ch) => ch.id === id);
        if (char !== -1) {
            const newCharacters = [...this.characters];
            newCharacters[char] = {
                ...this.characters[char],
                moods: this.characters[char].moods.filter((m) => m !== mood),
            };
            this.characters = newCharacters;
        }
    }
}

export const characterStore = new CharactersStore();
