import React, {
    FC,
    ReactNode,
    RefObject,
    useEffect,
    useRef,
    useState,
} from 'react';
import { coordsStore } from '../stores/coords-store';
import { observer } from 'mobx-react';
import { blockStore } from '../stores/block-store';

interface DraggableProps {
    children: (ref: RefObject<HTMLDivElement>, drag: boolean) => ReactNode;
    x?: number;
    y?: number;
    onDrag?: () => void;
    setCoords: (x: number, y: number) => void;
    id?: number;
}

export const Draggable: FC<DraggableProps> = observer(
    ({ children, x = 0, y = 0, onDrag, setCoords, id }) => {
        const ref = useRef<HTMLDivElement>(null);

        const [coordsOffset, setCoordsOffset] = useState([0, 0]);
        const [drag, setDrag] = useState(false);

        const mouseDown = (e: MouseEvent) => {
            setDrag(true);
            const targetBox = (
                e.target as HTMLElement
            )?.getBoundingClientRect?.();
            if (targetBox) {
                setCoordsOffset([
                    e.x - targetBox.x + coordsStore.coords[0],
                    e.y - targetBox.y + coordsStore.coords[1],
                ]);
            } else {
                setCoordsOffset([0, 0]);
            }
            setCoords(e.x, e.y);
        };

        const mouseUp = () => {
            if (drag) {
                if (id !== undefined) {
                    const block = blockStore.getBlock(id);
                    const { x: X = 0, y: Y = 0 } = block || {};
                    setCoords(X - coordsOffset[0], Y - coordsOffset[1]);
                } else {
                    setCoords(x - coordsOffset[0], y - coordsOffset[1]);
                }
                setDrag(false);
                setCoordsOffset([0, 0]);
            }
        };

        const onMove = (e: MouseEvent) => {
            if (drag) {
                setCoords(e.x, e.y);

                if (onDrag) {
                    onDrag();
                }
            }
        };

        const clearSelections = () => {
            window.getSelection()?.removeAllRanges();
        };

        useEffect(() => {
            ref.current?.addEventListener('mousedown', mouseDown);
            ref.current?.addEventListener('mouseup', clearSelections);

            return () => {
                ref.current?.removeEventListener('mousedown', mouseDown);
                ref.current?.removeEventListener('mouseup', clearSelections);
            };
        }, [ref.current]);

        useEffect(() => {
            document.addEventListener('mouseup', mouseUp);

            return () => document.removeEventListener('mouseup', mouseUp);
        }, [id, coordsOffset]);

        useEffect(() => {
            document.addEventListener('mousemove', onMove);

            return () => document.removeEventListener('mousemove', onMove);
        }, [drag]);

        return (
            <div
                style={{
                    position: 'absolute',
                    left: x - coordsOffset[0] + coordsStore.coords[0],
                    top: y - coordsOffset[1] + coordsStore.coords[1],
                }}
            >
                {children(ref, drag)}
            </div>
        );
    }
);
