import React, { Fragment, useEffect, useRef, useState } from 'react';
import { observer } from 'mobx-react';
import { blockStore, BlockType, languages } from './stores/block-store';
import { Start } from './components/start/start';
import { Line } from './components/line/line';

import s from './style.module.scss';
import { coordsStore } from './stores/coords-store';
import {
    Add,
    AddBox,
    AddCircle,
    Delete,
    Download,
    FilterCenterFocus,
    MenuOutlined,
    Save,
} from '@mui/icons-material';
import { Link } from './components/link/link';
import {
    Alert,
    Box,
    Button,
    Checkbox,
    Chip,
    Dialog,
    DialogActions,
    DialogContent,
    DialogContentText,
    DialogTitle,
    Divider,
    Drawer,
    FormControlLabel,
    Tab,
    Tabs,
    TextField,
    Tooltip,
    Typography,
} from '@mui/material';
import classNames from 'classnames';
import { dialogStore } from './stores/dialog-store';
import { characterStore } from './stores/characters-store';
import { Trigger } from './components/trigger/trigger';
import { localStorageStore } from './stores/local-storage-store';
import { moodStore } from './stores/mood-store';

export const App = observer(() => {
    const ref = useRef<HTMLDivElement>(null);
    const [move, setMove] = useState(false);
    const [coords, setCoords] = useState([0, 0]);
    const [globalCoords, setGlobalCoords] = useState([0, 0]);

    useEffect(() => {
        localStorageStore.reloadCharacters();
        localStorageStore.reloadDialog(localStorageStore.getCurrentDialogId());
        if (dialogStore.id === undefined) {
            console.log(dialogStore.id);
            localStorageStore.saveDialog(undefined);
        }
    }, []);

    const mouseDown = (e: MouseEvent) => {
        setMove(true);
        setCoords([e.x, e.y]);
        setGlobalCoords(coordsStore.coords);
    };

    const mouseUp = () => {
        setMove(false);
    };

    const clearSelections = () => {
        window.getSelection()?.removeAllRanges();
    };

    const onMove = (e: MouseEvent) => {
        if (move) {
            coordsStore.setCoords([
                globalCoords[0] + e.x - coords[0],
                globalCoords[1] + e.y - coords[1],
            ]);
        }
    };

    useEffect(() => {
        document.addEventListener('mousemove', onMove);
        return () => document.removeEventListener('mousemove', onMove);
    }, [coords, move]);

    useEffect(() => {
        ref.current?.addEventListener('mousedown', mouseDown);
        ref.current?.addEventListener('mouseup', clearSelections);
        document.addEventListener('mouseup', mouseUp);

        return () => {
            ref.current?.removeEventListener('mousedown', mouseDown);
            ref.current?.removeEventListener('mouseup', clearSelections);
            document.removeEventListener('mouseup', mouseUp);
        };
    }, [ref.current]);

    const [tab, setTab] = useState('dialog');
    const [sidebar, setSidebar] = useState(false);
    const [hack, setHack] = useState<undefined | number>();
    const [mood, setMood] = useState('');

    return (
        <div className={s.container}>
            <div className={classNames(s.controls, s.startControls)}>
                <Tooltip title="">
                    <div onClick={() => setSidebar(true)}>
                        <MenuOutlined />
                    </div>
                </Tooltip>
            </div>
            <Drawer
                anchor="left"
                open={sidebar}
                onClose={() => setSidebar(false)}
            >
                <div>
                    <Box
                        sx={{ borderBottom: 1, borderColor: 'divider' }}
                        style={{
                            position: 'sticky',
                            top: 0,
                            background: 'white',
                        }}
                    >
                        <Tabs
                            value={tab}
                            onChange={(e, value) => setTab(value)}
                        >
                            <Tab label="" value="dialog" />
                            <Tab label="" value="characters" />
                            <Tab label="" value="moods" />
                            <Tab label="" value="saves" />
                        </Tabs>
                    </Box>
                    {tab === 'dialog' && (
                        <div>
                            <div className={s.sidebarTab}>
                                <TextField
                                    value={dialogStore.name}
                                    onChange={(e) => {
                                        dialogStore.setName(e.target.value);
                                        if (dialogStore.id === undefined) {
                                            return;
                                        }
                                        localStorageStore.editSave(
                                            dialogStore.id,
                                            dialogStore.name
                                        );
                                    }}
                                    label=" "
                                />
                                {dialogStore.name &&
                                    dialogStore.id === undefined && (
                                        <Chip
                                            size="small"
                                            avatar={<Save />}
                                            label=""
                                            onClick={() => {
                                                localStorageStore.saveCurrentDialog(
                                                    dialogStore.name
                                                );
                                            }}
                                        />
                                    )}
                                {dialogStore.id !== undefined && (
                                    <Alert
                                        severity="success"
                                        sx={{ width: '100%' }}
                                    >
                                        
                                    </Alert>
                                )}
                                <Chip
                                    size="small"
                                    avatar={<Add />}
                                    label=" "
                                    onClick={() => {
                                        dialogStore.clear();
                                        blockStore.clear();
                                        localStorageStore.setCurrentDialog(
                                            undefined
                                        );
                                        coordsStore.setCoords([0, 0]);
                                    }}
                                />
                                <Chip
                                    size="small"
                                    avatar={<Download />}
                                    label=" JSON"
                                    onClick={() => {
                                        const data = {
                                            name: dialogStore.name,
                                            blocks: blockStore.blocks.map(
                                                ({
                                                    id,
                                                    type,
                                                    links,
                                                    content,
                                                }) => ({
                                                    id,
                                                    type,
                                                    links,
                                                    content:
                                                        type === BlockType.line
                                                            ? {
                                                                  ...content,
                                                                  currentOptionId:
                                                                      undefined,
                                                              }
                                                            : content,
                                                })
                                            ),
                                        };

                                        // 
                                        let a = document.createElement('a');
                                        let file = new Blob(
                                            [JSON.stringify(data)],
                                            {
                                                type: 'application/json',
                                            }
                                        );
                                        a.href = URL.createObjectURL(file);
                                        a.download =
                                            dialogStore.name
                                                .toLowerCase()
                                                .replace(
                                                    /[^\w--\-_]/g,
                                                    '_'
                                                ) + '.json';
                                        a.click();
                                    }}
                                />
                            </div>
                        </div>
                    )}
                    {tab === 'characters' && (
                        <div>
                            <div className={s.sidebarTab}>
                                <Chip
                                    size="small"
                                    avatar={<Download />}
                                    label=" JSON"
                                    onClick={() => {
                                        const data = characterStore.characters;

                                        // 
                                        const a = document.createElement('a');
                                        const file = new Blob(
                                            [JSON.stringify(data)],
                                            {
                                                type: 'application/json',
                                            }
                                        );
                                        a.href = URL.createObjectURL(file);
                                        a.download = 'characters.json';
                                        a.click();
                                    }}
                                />
                            </div>
                            <Divider />
                            {characterStore.characters.map((ch, i) => (
                                <Fragment key={i}>
                                    <div className={s.sidebarTab}>
                                        <Typography variant="h5">
                                            {Object.entries(ch.names)[0][1] ||
                                                ''}
                                        </Typography>
                                        <span
                                            style={{
                                                fontSize: 14,
                                                color: 'gray',
                                            }}
                                        >
                                            {'ID: ' + ch.id}
                                        </span>
                                        {Object.entries(ch.names).map(
                                            (entry) => (
                                                <div key={entry[0]}>
                                                    <TextField
                                                        value={entry[1]}
                                                        label={` [${entry[0]}]`}
                                                        fullWidth
                                                        onChange={(e) =>
                                                            characterStore.changeName(
                                                                ch.id,
                                                                entry[0],
                                                                e.target.value
                                                            )
                                                        }
                                                    />
                                                </div>
                                            )
                                        )}
                                        <span
                                            style={{
                                                fontSize: 14,
                                                color: 'gray',
                                            }}
                                        >
                                            :
                                        </span>
                                        <div className={s.moods}>
                                            {moodStore.moods.map((mood) => (
                                                <FormControlLabel
                                                    control={
                                                        <Checkbox
                                                            checked={ch.moods.includes(
                                                                mood
                                                            )}
                                                            onClick={() => {
                                                                if (
                                                                    ch.moods.includes(
                                                                        mood
                                                                    )
                                                                ) {
                                                                    characterStore.removeMood(
                                                                        ch.id,
                                                                        mood
                                                                    );
                                                                } else {
                                                                    characterStore.addMood(
                                                                        ch.id,
                                                                        mood
                                                                    );
                                                                }
                                                            }}
                                                        />
                                                    }
                                                    label={mood}
                                                />
                                            ))}
                                        </div>
                                        <Chip
                                            size="small"
                                            avatar={<Delete />}
                                            label=""
                                            onClick={() =>
                                                dialogStore.openDialog({
                                                    title: '',
                                                    text: '             .  ,      ,   .',
                                                    onAgree: () =>
                                                        characterStore.removeCharacter(
                                                            ch.id
                                                        ),
                                                })
                                            }
                                        />
                                    </div>
                                    <Divider />
                                </Fragment>
                            ))}
                            <div className={s.sidebarTab}>
                                <Chip
                                    size="small"
                                    avatar={<Add />}
                                    label=""
                                    onClick={() =>
                                        characterStore.addCharacter(
                                            languages.reduce(
                                                (a, l) => ({ ...a, [l]: '' }),
                                                {}
                                            )
                                        )
                                    }
                                />
                            </div>
                        </div>
                    )}
                    {tab === 'saves' && (
                        <div>
                            {localStorageStore.getSaves().map((save) => (
                                <div key={save.id}>
                                    <div className={s.sidebarTab}>
                                        <Typography
                                            variant="h5"
                                            onClick={() => {
                                                const goto = () => {
                                                    dialogStore.setSaveInProgress(
                                                        true
                                                    );
                                                    localStorageStore.reloadDialog(
                                                        save.id
                                                    );
                                                    localStorageStore.setCurrentDialog(
                                                        save.id
                                                    );
                                                    coordsStore.setCoords([
                                                        0, 0,
                                                    ]);
                                                    setSidebar(false);
                                                    dialogStore.setSaveInProgress(
                                                        false
                                                    );
                                                };

                                                if (
                                                    dialogStore.id === undefined
                                                ) {
                                                    dialogStore.openDialog({
                                                        title: '',
                                                        text: '   ?   .',
                                                        onAgree: goto,
                                                    });
                                                } else {
                                                    goto();
                                                }
                                            }}
                                            style={{ cursor: 'pointer' }}
                                        >
                                            {save.name}
                                        </Typography>
                                        <Chip
                                            size="small"
                                            avatar={<Delete />}
                                            label=""
                                            onClick={() =>
                                                dialogStore.openDialog({
                                                    title: '',
                                                    text: '     ?',
                                                    onAgree: () => {
                                                        localStorageStore.removeSave(
                                                            save.id
                                                        );
                                                        //    
                                                        setHack(Date.now());
                                                    },
                                                })
                                            }
                                        />
                                    </div>
                                    <Divider />
                                </div>
                            ))}
                        </div>
                    )}
                    {tab === 'moods' && (
                        <div>
                            <div className={s.sidebarTab}>
                                <TextField
                                    value={mood}
                                    onChange={(e) => setMood(e.target.value)}
                                    label=" "
                                />
                                <Chip
                                    size="small"
                                    avatar={<Add />}
                                    label=""
                                    onClick={() => {
                                        moodStore.addMood(mood);
                                        setMood('');
                                    }}
                                />
                            </div>
                            <Divider />
                            {moodStore.moods.map((mood) => (
                                <div>
                                    <div key={mood} className={s.mood}>
                                        <div>{mood}</div>
                                        {mood !== 'default' && (
                                            <div
                                                onClick={() =>
                                                    moodStore.removeMood(mood)
                                                }
                                            >
                                                <Delete />
                                            </div>
                                        )}
                                    </div>
                                    <Divider />
                                </div>
                            ))}
                        </div>
                    )}
                </div>
            </Drawer>
            <div ref={ref} className={s.moveArea}>
                <div
                    className={s.startPoint}
                    style={{
                        left: coordsStore.coords[0],
                        top: coordsStore.coords[1],
                    }}
                >
                    0
                </div>
            </div>
            {blockStore.blocks.map((block) => (
                <Fragment key={block.id}>
                    {(() => {
                        if (block.type === BlockType.start) {
                            return <Start data={block} />;
                        }
                        if (block.type === BlockType.line) {
                            return <Line data={block} />;
                        }
                        if (block.type === BlockType.trigger) {
                            return <Trigger data={block} />;
                        }
                    })()}
                    {block.links &&
                        Object.entries(block.links).map((entry) => {
                            const linkedBlock = blockStore.blocks.find(
                                (b) => b.id === entry[1]
                            );
                            return (
                                <Link
                                    key={block.id + entry[0]}
                                    id={block.id}
                                    option={entry[0]}
                                    x1={
                                        entry[0] === '_default'
                                            ? block.endX
                                            : block?.optionsCoords?.[
                                                  entry[0]
                                              ]?.[0]
                                    }
                                    y1={
                                        entry[0] === '_default'
                                            ? block.endY
                                            : block?.optionsCoords?.[
                                                  entry[0]
                                              ]?.[1]
                                    }
                                    x2={linkedBlock?.startX}
                                    y2={linkedBlock?.startY}
                                />
                            );
                        })}
                </Fragment>
            ))}
            <div className={classNames(s.controls, s.endControls)}>
                <Tooltip title="  ">
                    <div onClick={() => coordsStore.setCoords([0, 0])}>
                        <FilterCenterFocus />
                    </div>
                </Tooltip>
                <Tooltip title=" ">
                    <div
                        onClick={() => blockStore.addBlock(BlockType.line)}
                        style={{ top: 80 }}
                    >
                        <AddBox />
                    </div>
                </Tooltip>
                <Tooltip title=" ">
                    <div
                        onClick={() => blockStore.addBlock(BlockType.trigger)}
                        style={{ top: 140 }}
                    >
                        <AddCircle />
                    </div>
                </Tooltip>
            </div>
            <Dialog
                open={!!dialogStore.dialog?.open}
                onClose={() => dialogStore.dialogDisagree()}
                aria-labelledby="alert-dialog-title"
                aria-describedby="alert-dialog-description"
            >
                <DialogTitle id="alert-dialog-title">
                    {dialogStore.dialog?.title}
                </DialogTitle>
                <DialogContent>
                    <DialogContentText id="alert-dialog-description">
                        {dialogStore.dialog?.text}
                    </DialogContentText>
                </DialogContent>
                <DialogActions>
                    <Button onClick={() => dialogStore.dialogDisagree()}>
                        
                    </Button>
                    <Button
                        onClick={() => dialogStore.dialogAgree()}
                        variant="contained"
                        autoFocus
                    >
                        
                    </Button>
                </DialogActions>
            </Dialog>
        </div>
    );
});
